/**
 * breakpoint-up/down - Change color of a css property based on the currently active theme
 *
 * @param  {size}     min/max size of the screen
 *
 * @example:-
 *
 * HIDE FOR DESKTOP:
 @include breakpoint-down(small) {
    background: red;
 }
 * HIDE FOR MOBILE:
 @include breakpoint-up(medium) {
    background: blue;
 }
 *
 * if you are designing your layout with a mobile-first approach, then use min-width breakpoints and work your way up.
 * if you are designing for larger devices first, then set your default CSS as you normally would and adjust for smaller devices with the max-width approach.
*/
.cust-hero {
  display: flex;
  align-items: center;
  flex-direction: column;
}
.cust-hero-main {
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-top: 100px;
  position: relative;
  width: 100%;
}
.cust-hero-main::before {
  position: absolute;
  content: "";
  top: 18%;
  left: 0;
  width: 100%;
  height: 5.1875rem;
  background-color: #E8EFFA;
  z-index: -1;
}
.cust-hero-main__sub-heading {
  font-size: 2.0625em;
  font-style: var(--font-heading-family);
}
.cust-hero-collection {
  display: flex;
  gap: 1.125rem;
  margin-top: 8.25rem;
  margin-bottom: 13.25rem;
}
@media (max-width: 67.125em) {
  .cust-hero-collection {
    display: flex;
    align-items: center;
    flex-direction: column;
  }
}
.cust-hero-collection__card {
  width: 330px;
  height: 270px;
  background-color: #E8EFFA;
  border-radius: 0.4375rem;
  border: 0.1875rem solid #1E1E1E;
  text-decoration: none;
  position: relative;
}
.cust-hero-collection__card--bg {
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 100%;
  width: 100%;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  opacity: 1;
  transition: opacity 0.3s;
  z-index: 0;
}
.cust-hero-collection__card--hover {
  display: block;
  position: absolute;
  background-size: 60%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 100%;
  width: 100%;
  background-repeat: no-repeat;
  background-position: center;
  opacity: 0;
  transition: none;
}
.cust-hero-collection__card--title {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -300%);
  color: #fff;
  font-weight: 900;
  font-size: 4.25rem;
  line-height: 2.6875rem;
  height: 2.6875rem;
  text-align: center;
  opacity: 1;
  transition: opacity 0.3s;
  z-index: 1;
}
.cust-hero-collection__card:hover > .cust-hero-collection__card--hover {
  opacity: 1;
  transform: opacity 0.3s;
}
.cust-hero-collection__card:hover > .cust-hero-collection__card--title, .cust-hero-collection__card:hover > .cust-hero-collection__card--bg {
  opacity: 0;
  transition: none;
}